/* pt-mono-regular - latin_cyrillic */
@font-face {
  font-family: "PT Mono";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/pt-mono-v7-latin_cyrillic-regular.eot");
  src: local("PT Mono"),
    local("PTMono-Regular"),
    url("../fonts/pt-mono-v7-latin_cyrillic-regular.eot?#iefix") format("embedded-opentype"),
    url("../fonts/pt-mono-v7-latin_cyrillic-regular.woff2") format("woff2"),
    url("../fonts/pt-mono-v7-latin_cyrillic-regular.woff") format("woff"),
    url("../fonts/pt-mono-v7-latin_cyrillic-regular.ttf") format("truetype"),
    url("../fonts/pt-mono-v7-latin_cyrillic-regular.svg#PTMono") format("svg");
}

/* roboto-condensed-regular - latin_cyrillic */
@font-face {
  font-family: "Roboto Condensed";
